import jsPDF from "jspdf";
import "jspdf-autotable";
import {
  getToday,
  localStoragedecryp,
} from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const CetakLaporanServiceAmbilPdf = (
  data = "",
  username = "",
  tanggal_dari = "",
  tanggal_sampai = ""
) => {
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  // let footRows = [];
  let tableColumn = [];
  let tbl_barang = [];
  let finalY = 30;
  let total_baraang = 0;
  let total_berat = 0;
  let total_jumlah = 0;
  let total_ongkos = 0;

  doc.setFontSize(15);
  doc.text("LAPORAN SERVICE AMBIL", 14, 15);
  doc.setFontSize(10);
  doc.setProperties({
    title: "LAPORAN SERVICE AMBIL",
  });
  //row 1
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );
  tableColumn = [
    [
      {
        content: `TANGGAL AMBIL`,
      },
      {
        content: `TANGGAL SELESAI`,
      },
      {
        content: `KODE SALES`,
      },
      {
        content: `NAMA CUSTOMER`,
      },
      {
        content: `NO HP`,
      },
      {
        content: `ALAMAT CUSTOMER`,
      },
      {
        content: `KODE GUDANG`,
      },
      {
        content: `TOTAL SERVICE`,
      },
    ],
  ];
  data.forEach((item, index) => {
    item._id.pembayaran.forEach((header_detail, index) => {
      // console.log(item._id.pembayaran[0].jenis);
      // \n${
      //   header_detail.jenis
      // }  (${parseInt(header_detail.jumlah_rp).toLocaleString("ID-id")})
      let no_services = [
        {
          content: `No Services : ${item._id.no_faktur_service}`,
          colSpan: 7,
        },
      ];

      tableRows.push(no_services);
    });

    item.detail.forEach((detail, index) => {
      let rows = [
        detail.tgl_ambil,
        detail.tgl_selesai,
        detail.kode_sales,
        detail.nama_customer,
        detail.no_hp,
        detail.alamat_customer,
        detail.kode_gudang,
        detail.total.toLocaleString("ID-id"),
      ];
      tableRows.push(rows);
      detail.detail_barang.forEach((detail_barang, index) => {
        tbl_barang = [
          {
            content: `Detail Barang`,
            colSpan: 7,
            styles: {
              halign: "center",
              // fillColor: "#E8E5E5",
              // textColor: "#000",
            },
          },
        ];
        tableRows.push(tbl_barang);

        let tbLhead = [
          {
            content: `Nama Barang`,
            colSpan: 2,
            styles: {
              fillColor: "#E8E5E5",
              textColor: "#000",
            },
          },
          {
            content: `BERAT`,
            styles: {
              fillColor: "#E8E5E5",
              textColor: "#000",
            },
          },
          {
            content: `JUMLAH`,
            styles: {
              fillColor: "#E8E5E5",
              textColor: "#000",
            },
          },
          {
            content: `Ongkos`,
            styles: {
              fillColor: "#E8E5E5",
              textColor: "#000",
            },
          },
          {
            content: `Deskripsi`,
            colSpan: 3,
            styles: {
              fillColor: "#E8E5E5",
              textColor: "#000",
            },
          },
        ];
        tableRows.push(tbLhead);
        total_ongkos =
          total_ongkos +
          parseInt(detail_barang.ongkos) * parseInt(detail_barang.jumlah);
        total_jumlah = total_jumlah + detail_barang.jumlah;
        total_berat = total_berat + detail_barang.berat;
        let brg = [
          {
            content: detail_barang.nama_barang,
            colSpan: 2,
          },
          detail_barang.berat.toFixed(1),
          detail_barang.jumlah,
          (
            parseInt(detail_barang.ongkos) * parseInt(detail_barang.jumlah)
          ).toLocaleString("ID-id"),
          {
            content: detail_barang.deskripsi,
            colSpan: 2,
          },
        ];
        tableRows.push(brg);

        // let detail_total = [
        //   {
        //     content: `Total Barang ${detail.detail_barang.length}`,
        //     colSpan: 2,
        //     styles: {
        //       halign: "center",
        //       fillColor: "#E8E5E5",
        //       textColor: "#000",
        //     },
        //   },
        //   {
        //     content: `${total_berat}`,
        //     styles: {
        //       fillColor: "#E8E5E5",
        //       textColor: "#000",
        //     },
        //   },
        //   {
        //     content: `${total_jumlah}`,
        //     styles: {
        //       fillColor: "#E8E5E5",
        //       textColor: "#000",
        //     },
        //   },
        // ];
        // tableRows.push(detail_total);
      });
      total_baraang = total_baraang + detail.detail_barang.length;
    });

    // localStorage.setItem('hasil',JSON.stringify(item.detail.length))
    // console.log();
    // let footer = [``, ``, ""];
    // footRows.push(footer);
  });

  let printed = [
    {
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      colSpan: 7,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);

  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    // foot: tbl_barang,
    startY: finalY,
    theme: "plain",
    rowPageBreak: "avoid",
    // pageBreak: "avoid",
    margin: { top: 10 },
    footStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    bodyStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
      // lineWidth: 0.02,
      // lineColor: "#000",
      fillColor: "#E8E5E5",
      textColor: "#000",
    },
    tableLineColor: [255, 255, 255],
    tableLineWidth: 1,
  });
  finalY = doc.autoTableEndPosY() + 3;
  tableRows = [];
  // footRows = [];
  // tbl_barang = [];
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  // doc.text(
  //   `User                          : ${
  //     JSON.parse(localStoragedecryp("userdata")).user_id
  //   }`,
  //   14,
  //   finalY + 10
  // );
  // doc.text(`Cetak                        : ${getToday()}`, 14, finalY + 16);
  doc.text(
    `Total Berat                : ${total_berat.toFixed(3)}`,
    14,
    finalY + 22
  );
  doc.text(`Total Jumlah             : ${total_jumlah}`, 14, finalY + 28);
  doc.text(
    `Total Ongkos            : ${parseInt(total_ongkos).toLocaleString(
      "ID-id"
    )}`,
    14,
    finalY + 34
  );
  doc.text(`Total Barang             : ${total_baraang}`, 14, finalY + 40);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`LAPORAN SERVICES AMBIL.pdf`);
  var string = doc.output("datauristring");
  // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN SERVICES AMBIL</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default CetakLaporanServiceAmbilPdf;
